
'use client';

import { useState, useEffect } from 'react';
import type { JournalEntry } from '@/types';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { format, parseISO } from 'date-fns';
import { ArrowLeft, BookOpenText, Loader2, Lightbulb, AlertTriangle, MessageSquareHeart, Info, Target, ShieldCheck, Brain, Heart, Sun } from 'lucide-react';

const JOURNAL_HISTORY_KEY = 'reflectifyJournalHistory';

export default function HistoryPage() {
  const [journalHistory, setJournalHistory] = useState<JournalEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(JOURNAL_HISTORY_KEY);
      if (storedHistory) {
        const parsedHistory: JournalEntry[] = JSON.parse(storedHistory);
        // Filter for processing entries: includes all three process types
        const processingEntries = parsedHistory.filter(
          entry => entry.isFailProof === true || 
                   (entry.failureReason && entry.failureReason.trim() !== '') ||
                   entry.goalWantRating !== undefined ||
                   (entry.bestCaseScenarioText && entry.bestCaseScenarioText.trim() !== '')
        );
        processingEntries.sort((a, b) => parseISO(b.date).getTime() - parseISO(a.date).getTime());
        setJournalHistory(processingEntries);
      }
    } catch (error) {
      console.error("Failed to load or filter journal history from localStorage:", error);
    }
    setIsLoading(false);
  }, []);

  const getCategoryDescription = (category?: string) => {
    if (!category) return null;
    switch (category) {
      case 'POSSIBLE_OUTCOME':
        return 'Potential external event/outcome';
      case 'PAST_TRAIT':
        return 'Personal trait/past behavior';
      case 'ABSOLUTE_TRUTH':
        return 'Perceived unchangeable fact/limiting belief';
      default:
        return 'General';
    }
  };

  if (isLoading) {
    return (
      <div className="container mx-auto max-w-3xl py-8 px-4 flex flex-col justify-center items-center min-h-[calc(100vh-150px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary mb-4" />
        <p className="text-lg text-muted-foreground">Loading processing history...</p>
      </div>
    );
  }

  return (
    <div className="container mx-auto max-w-3xl py-8 px-4">
      <div className="mb-8 flex items-center justify-between">
        <h1 className="text-3xl font-bold text-foreground flex items-center">
          <Brain className="mr-3 h-8 w-8 text-primary" /> Processing Summaries
        </h1>
        <Button asChild variant="outline" size="sm">
          <Link href="/processing" className="flex items-center">
            <ArrowLeft className="mr-2 h-4 w-4" />
            Back to Processing
          </Link>
        </Button>
      </div>

      {journalHistory.length === 0 ? (
        <Card className="shadow-lg rounded-xl text-center py-10">
          <CardHeader className="pb-2">
            <BookOpenText className="mx-auto h-16 w-16 text-muted-foreground mb-4" />
            <CardTitle className="text-xl">No Processing Summaries Yet</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground mb-6">
              You haven&apos;t saved any goal processing summaries. Process a goal on the Processing page!
            </p>
            <Button asChild>
              <Link href="/processing">Start Processing</Link>
            </Button>
          </CardContent>
        </Card>
      ) : (
        <div className="space-y-6">
          {journalHistory.map((entry) => (
            <Card key={entry.id} className="shadow-md rounded-lg overflow-hidden">
              <CardHeader className="bg-card">
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle className="text-lg font-semibold text-foreground">
                        Processed on: {format(parseISO(entry.date), 'EEEE, MMMM dd, yyyy')}
                        </CardTitle>
                        <CardDescription className="text-sm text-muted-foreground">
                        {format(parseISO(entry.date), 'p')}
                        </CardDescription>
                    </div>
                </div>
              </CardHeader>
              <CardContent className="pt-4 space-y-4">
                {/* Goal Desirability Section */}
                {entry.goalWantRating !== undefined && (
                  <div className="p-4 bg-secondary/30 rounded-md border border-secondary/50">
                    <h4 className="font-semibold text-secondary-foreground text-md mb-2 flex items-center">
                      <Heart className="h-5 w-5 text-secondary-foreground mr-2" />Goal Desirability:
                    </h4>
                    <p className="text-sm text-foreground">
                      <span className="font-medium">Desire Rating:</span> {entry.goalWantRating}/10
                    </p>
                    {entry.goalWantRating < 10 && entry.whyNotTenRatingReason && (
                      <p className="text-sm text-foreground mt-1">
                        <span className="font-medium">Reason for not 10/10:</span> {entry.whyNotTenRatingReason}
                      </p>
                    )}
                    {entry.goalWantRating < 10 && entry.goalWantReasons && (
                       <p className="text-sm text-foreground mt-1 whitespace-pre-wrap">
                        <span className="font-medium">Reasons for wanting goal:</span> {entry.goalWantReasons}
                      </p>
                    )}
                  </div>
                )}

                {/* Best Case Scenario Section */}
                {entry.bestCaseScenarioText && (
                  <div className="p-4 bg-accent/10 rounded-md border border-accent/30">
                    <h4 className="font-semibold text-accent-foreground text-md mb-2 flex items-center">
                      <Sun className="h-5 w-5 text-accent-foreground mr-2" />Best Case Scenario:
                    </h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">
                      {entry.bestCaseScenarioText}
                    </p>
                  </div>
                )}

                {/* Impossible to Fail / Standard 10F Section */}
                {entry.isFailProof === true && (
                  <div className="p-4 bg-accent/10 rounded-md border border-accent/30 flex items-center">
                    <ShieldCheck className="h-6 w-6 text-accent mr-3 shrink-0" />
                    <div>
                      <h4 className="font-semibold text-accent-foreground text-md">Goal Confidence:</h4>
                      <p className="text-sm text-foreground">This goal was marked as fail-proof during processing. You now know it is impossible to fail.</p>
                    </div>
                  </div>
                )}

                {entry.isFailProof !== true && entry.failureReason && (
                  <>
                    <div className="p-3 bg-destructive/10 rounded-md border border-destructive/30">
                      <h4 className="font-semibold text-destructive mb-1 text-md">Potential Failure Analysis:</h4>
                      <p className="text-sm text-foreground whitespace-pre-wrap"><span className="font-medium">Original Reason:</span> {entry.failureReason}</p>
                      {entry.failureCategory && entry.categorizedFailureStatement && (
                         <div className="mt-2 p-2 bg-muted/20 rounded-md border">
                           <p className="text-xs text-foreground"><Info className="inline-block mr-1 h-3 w-3 text-primary"/> <span className="font-semibold">Categorization ({getCategoryDescription(entry.failureCategory)}):</span> {entry.categorizedFailureStatement}</p>
                         </div>
                      )}
                    </div>
                    
                    <Card className="bg-card mt-4">
                        <CardHeader className="pb-2 pt-4">
                            <CardTitle className="text-md font-semibold text-foreground">10F Processing Statements:</CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-3">
                            {entry.leftHandText && (
                            <div className="p-2 bg-muted/30 rounded-md border border-destructive/40">
                                <p className="text-sm text-foreground"><AlertTriangle className="inline-block mr-2 h-4 w-4 text-destructive"/> <span className="font-semibold">Left Hand:</span> {entry.leftHandText}</p>
                            </div>
                            )}
                            {entry.rightHandText && (
                            <div className="p-2 bg-muted/30 rounded-md border border-accent/40">
                                <p className="text-sm text-foreground"><Lightbulb className="inline-block mr-2 h-4 w-4 text-accent"/> <span className="font-semibold">Right Hand:</span> {entry.rightHandText}</p>
                            </div>
                            )}
                        </CardContent>
                    </Card>
                  </>
                )}
                
                {/* Common Sections: Insights and Goal Improvement */}
                {entry.insightsText && (
                  <div className="p-3 bg-primary/10 rounded-md border border-primary/30 mt-4">
                     <h4 className="font-semibold text-primary-foreground mb-1 text-md flex items-center">
                        <MessageSquareHeart className="mr-2 h-5 w-5" /> Insights and Realizations:
                    </h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">{entry.insightsText}</p>
                  </div>
                )}

                {entry.goalImprovementText && (
                  <div className="p-3 bg-accent/10 rounded-md border border-accent/30 mt-4">
                    <h4 className="font-semibold text-accent-foreground mb-1 text-md flex items-center">
                        <Target className="mr-2 h-5 w-5" /> Goal Improvement Ideas:
                    </h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">{entry.goalImprovementText}</p>
                  </div>
                )}
                
                {/* Fallback for entries that might not fit neatly into the above sections after new processes are added */}
                {!entry.isFailProof && !entry.failureReason && !entry.goalWantRating && !entry.bestCaseScenarioText && !entry.insightsText && !entry.goalImprovementText && (
                    <p className="text-sm text-muted-foreground italic mt-2">Processing details not applicable or not recorded for this entry.</p>
                )}

              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
